___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: #f6f6ef; }

ul,
li {
  list-style-type: none; }

body {
  background-color: #121212;
  color: #f6f6ef;
  font-family: poppins; }

.flex {
  display: flex;
  align-items: center; }

.btn {
  background-color: #2776ff;
  padding: .5rem;
  padding-inline: .8rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer; }

.btn-secondary {
  background-color: white;
  padding: 1rem;
  padding-inline: 1.5rem;
  color: black;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer; }

.view-more-btn {
  background-color: transparent;
  padding: .5rem 1rem;
  border: 2px solid white;
  border-radius: .5rem;
  color: white;
  font-family: poppins;
  cursor: pointer; }
